*{
    padding: 0;
    margin: 0;
    font-family: 'Montserrat', sans-serif;
    box-sizing: border-box;
}

:root {
    --box_color: #1b1c1e;
    --text_color: #787879;
    --box_heading_color: #3f4349;
    --center-width: 90%;
    --line_hight: 1.5;
    --bacground_color: #141416;
    --web_hilight_color:#6239f2;
}

/* nav_bar styled */

.nav_container{
    position: relative;
    width: 100%;  
}

.inner-container{
    width: 100%;
    background-color: #131517;
    padding: 20px 70px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    transition: all 1s ease;
    position: fixed;
    width: 100%;
    top: 0;
    left: 0;
    opacity: 1;
    z-index: 2211;
}

.img-div img{
    width: 150px;
}

.nav_list {
    display: flex;
    list-style: none;
    font-family: 'Roboto', sans-serif;
}

.nav_list li a{
    text-decoration: none;
    font-size: 15px;
    font-weight: 500;
    padding-left: 20px;
    color: whitesmoke;
    transition: all .5s ease;
}

.nav_list li a:focus{
    color: #6239f2;
    color: var(--web_hilight_color);
}
.nav_list li a:hover{
    color: rgb(139, 137, 137);
}

