/* ------------- footer styles */
footer{
    display: flex;
    justify-content: space-between;
    padding: 61px 45px;
    box-sizing: border-box;
    background:#141416;
    background:var(--bacground_color);
    color: #787879;
    color: var(--text_color);
    position: relative;
    z-index: 111;
}

.footer_logo_bar{
    width: 45%;
    display: flex;
    justify-content: space-between;
}

.footer_logo_bar .footer_sit_link p{
    padding-top: 10px;
    padding-bottom: 70px;
}

.footer_logo_bar div img{
    width: 164px;
}

.footer_logo_bar .footer_sit_link a{
    font-family: sans-serif;
    color: #787879;
    color: var(--text_color);
}

.footer_logo_bar ul{
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;
    list-style: none;
    line-height: 3;
}

.footer_logo_bar ul li a{
    text-decoration: none;
    color: #787879;
    color: var(--text_color);
    font-family: sans-serif;
}




.footer_contact_bar{
    width: 35%;
    padding: 0 0 0 96px;
    text-align: justify;
    line-height: 2;
}

.footer_contact_bar p{
    font-family: sans-serif;
}

.footer_contact_bar .footer_icons{
    padding-top: 4rem;
}
.footer_contact_bar .footer_icons a{
    padding-right: 20px;
    font-size: 20px;
    color: white;
}






