.connect_header{
    background-image: url(/casper_demo_web/static/media/connect.00bcbc01.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;
    width: 100%;
    height: 100vh;
}

.connect_header .title_div{
    position: absolute;
    top: 40%;
    left: 70px;
}

.connect_header .title{
    /* background: rgba(255, 0, 0, 0.548); */
    height: 80px;
    width: 840px;
    font-size: 30px;
    letter-spacing: -1px;
    overflow: hidden;
    animation: writing 500ms ease-in;
}

@keyframes writing {
    0%{
        width: 0px;
        border-right: 5px solid rgb(17, 16, 16);
    }
    100%{
        border-right: 5px solid rgb(3, 3, 3);
        width: 800px; ;
    }
}


