/* --------------- Home 1st page styled */
.main_container{
    background: #141416;
    background: var(--bacground_color);
    position: relative;
    z-index: 111;
}

.Home_page_1st{
    width: 90%;
    width: var(--center-width);
    display: flex;
    justify-content: space-between;
    position: relative;
    top: -79px;
    margin: auto;
    /* margin-top: 174px; */
    background: #141416;
    background: var(--bacground_color);
}

.title_content{
    width: 40%;
    padding-left: 70px;
    padding-bottom: 15rem;
    background-image: url(data:image/jpeg;base64,);
    background-image: var(--content_1st_bg);
    background-repeat: no-repeat;
    background-size: cover;
}

.content_boxes{
    width: 45%;
    background: #141416;
    background: var(--bacground_color);
    position: relative;
}

/* 1st page in Content1st styled*/

.content1st_content{
    padding: 0px 258px 10px 10px;
    position: relative;
    transition: all 500ms ease-in;
}
.content1st_content h1{
    color: white;
    padding: 25px 0;
    position: relative;
    transition: all 500ms ease-in;
}
.content1st_content h4{
    position: relative;
    color: #787879;
    color: var(--text_color);
    line-height:1.5;
    line-height:var(--line_hight);
    transition: all 500ms ease-in;
}

/* 1st page in Content2st styled*/

.content2nd_boxe2{
    width: 60%;
    color: rgb(133, 127, 127);
    background: #1b1c1e;
    background: var(--box_color);
    margin-bottom: 5px;
    padding: 35px 1.5rem;
    border-radius: 20px;
    position: relative;
    transition: all 500ms ease-in;
}

.content2nd_boxe2 h4{
    position: relative;
}

.content2nd_boxe2 h4::after{
    content: "";
    width: 100%;
    height: 6px;
    background: #3f4349;
    background: var(--box_heading_color);
    border-radius: 8px;
    position: absolute;
    bottom: -20%;
    left: 0%;
}

.content2nd_boxe{
    width: 35%;
    color: rgb(133, 127, 127);
    background: #1b1c1e;
    background: var(--box_color);
    margin-bottom: 5px;
    padding: 35px 1.5rem;
    border-radius: 20px;
    position: relative;
    transition: all 500ms ease-in;
}

.content2nd_boxe .icon_div i ,.icon_div_2 i{
    color: #6239f2;
    font-size: 50px;
    padding-bottom: 30px;
}
.content2nd_boxe h2, .content2nd_boxe2 h2{
    color: white;
    padding-bottom: 10px;
    line-height: 1.5;
    line-height: var(--line_hight);
}

.content2nd_boxe h4{
    color: #787879;
    color: var(--text_color);
    line-height: 1.5;
    line-height: var(--line_hight);
    position: relative;
}

.content2nd_boxe h4::after{
    content: "";
    width: 100%;
    height: 6px;
    background: #3f4349;
    background: var(--box_heading_color);
    border-radius: 8px;
    position: absolute;
    bottom: -20%;
    left: 0%;
}

.box_1{
    width: 100%;
