}

/* ------------- Home page 2nd */

.Home_page_2nd {
    width: 90%;
    width: var(--center-width);
    margin: auto;
    /* margin-top: 100px; */
    background: #141416;
    background: var(--bacground_color);
    color: white;
    display: flex;
    justify-content: space-between;
    position: relative;
    transition: all 500ms ease-in;
}

.page_2nd_content_1st{
    width: 50%;
    background-image: url(/casper_demo_web/static/media/bg-cont.8fc15140.jpg);
    background-image: var(--page_2_c_bg);
    background-repeat: no-repeat;
    background-size: cover;
    padding: 140px 207px;
    text-align: center;
    box-sizing: border-box;
    position: relative;
    transition: all 500ms ease-in;
}

.page_2nd_content_2nd{
    width: 50%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 0 100px;
    padding-right: 329px;
    /* height: 100vh; */
    box-sizing: border-box;
}

.page_2nd_content_2nd h4{
    color: #787879;
    color: var(--text_color);
}

/*--------------- Section_1st styled */

.section_1st{
    background-image: url(/casper_demo_web/static/media/bg-footer.e7367668.jpg);
    background-image: var(--section_1st_bg);
    background-repeat: no-repeat;
    background-size: cover;
    margin-top: 0;
    margin-bottom: 0;
    color: white;
    padding: 70px 150px 150px 150px;
    text-align: center;
    position: relative;
    transition: all 500ms ease-in;
}

.section_1st h1{
    font-size: 30px;
    font-weight: bolder;
}

.tokonomic_section_1st{
    display: flex;
    justify-content: space-evenly;
    position: relative;
}

.persentageSec_1st{
    padding: 30px 70px;
    position: relative;
    transition: all 500ms ease-in;
}

.persentageSec_1st h1{
    font-size: 60px;
    font-weight: bolder;
    padding: 7px 0;
}
.persentageSec_1st p{
    padding: 5px 0;
}

/* tokonomic_section_2st */
.tokonomic_section_2st{
    display: flex;
    justify-content: space-evenly;
    margin-top: 50px;
    position: relative;
}

.persentageSec_2st{
    padding: 30px 121px;
    position: relative; 
    transition: all 700ms ease-in;
}

.persentageSec_2st h1{
    font-size: 60px;
    font-weight: bolder;
    padding: 7px 0;
}

.persentageSec_2st p{
    padding: 5px 0;
}

/* persentageSec_1st and persentageSec_2st both heading*/
.Sect_1st_div .after_slide,.Sect_2st_div .after_slide{
    width: 10%;
    height: 7px;
    background: #3f4349;
    background: var(--box_heading_color);
    position: relative;
    bottom: 0px;
    left: 0;
    border-radius: 4px;
    margin: 5px;
    transition: all 900ms ease-in;
}

/* SECTION - 2 START */

.section_2{
    /* background: red;     */
    position: relative;
}

.section_2_1st_div{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    padding: 50px 0;
    background: #141416;
    background: var(--bacground_color);
}

.section_2_1st_div h1{
    font-size: 35px;
    color: white;
    position: relative;
    animation: slide 700ms ease-in;
}
@keyframes slide{
    0%{left: -50px; opacity: 0;}
    100%{left: 0px; opacity: 1;}
}

.section_2_1st_div p img{
    width: 299px;
}

.section_2_2nd_div{
    display: flex;
    justify-content: center;
    align-items: center;
