    background: var(--bacground_color);
}

.section_2_3rd_div{
    background: #141416;
    background: var(--bacground_color);
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 36px 0px;
}

.section_2_4th_div{
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: 36px 0px;
    background: #141416;
    background: var(--bacground_color);
}





